// 后台路径配置工具

import { getRuntimeConfig } from './runtimeConfig'

// 写死的后台路径
const ADMIN_PATH = 'admins';

// 获取当前后台路径（写死为 admins）
export const getAdminPath = (): string => {
  return ADMIN_PATH;
};

// 获取 API URL（用于 API 调用）
export const getApiUrl = (): string => {
  const config = getRuntimeConfig();
  return config.apiUrl;
};

// 生成后台路由 URL
export const getAdminUrl = (path: string = ''): string => {
  const cleanPath = path.startsWith('/') ? path.slice(1) : path;
  return `/${ADMIN_PATH}${cleanPath ? `/${cleanPath}` : ''}`;
};

// 获取后台路由对象（用于 useRouter 的 push 方法）
export const getAdminRoute = (path: string = ''): string => {
  return getAdminUrl(path);
};

// 检查当前路径是否为后台路径
export const isAdminPage = (pathname: string): boolean => {
  // 服务端渲染时，直接检查路径
  // 排除 /note/ 路径（笔记详情页）
  if (pathname.startsWith('/note/')) {
    return false;
  }

  // 检查是否为后台路径
  return pathname.startsWith(`/${ADMIN_PATH}`);
};