import { type ClassValue, clsx } from 'clsx'
import clientCache from './clientCache'

/**
 * 合并 className
 */
export function cn(...inputs: ClassValue[]) {
  return clsx(inputs)
}

/**
 * 格式化日期
 */
export function formatDate(date: string | Date, format: string = 'YYYY-MM-DD'): string {
  const d = new Date(date)
  const year = d.getFullYear()
  const month = String(d.getMonth() + 1).padStart(2, '0')
  const day = String(d.getDate()).padStart(2, '0')
  const hours = String(d.getHours()).padStart(2, '0')
  const minutes = String(d.getMinutes()).padStart(2, '0')
  const seconds = String(d.getSeconds()).padStart(2, '0')

  return format
    .replace('YYYY', String(year))
    .replace('MM', month)
    .replace('DD', day)
    .replace('HH', hours)
    .replace('mm', minutes)
    .replace('ss', seconds)
}

/**
 * 格式化相对时间
 */
export function formatRelativeTime(date: string | Date): string {
  const now = new Date()
  const target = new Date(date)
  const diff = now.getTime() - target.getTime()

  const seconds = Math.floor(diff / 1000)
  const minutes = Math.floor(seconds / 60)
  const hours = Math.floor(minutes / 60)
  const days = Math.floor(hours / 24)
  const months = Math.floor(days / 30)
  const years = Math.floor(days / 365)

  if (seconds < 60) return '刚刚'
  if (minutes < 60) return `${minutes}分钟前`
  if (hours < 24) return `${hours}小时前`
  if (days < 30) return `${days}天前`
  if (months < 12) return `${months}个月前`
  return `${years}年前`
}

/**
 * 截断文本
 */
export function truncate(text: string, length: number = 100): string {
  if (text.length <= length) return text
  return text.slice(0, length) + '...'
}

/**
 * 获取 API 基础 URL
 * 支持自动识别当前域名，无需手动配置
 */
export function getApiBaseUrl(): string {
  // 如果配置了完整的API URL，直接使用
  if (process.env.NEXT_PUBLIC_API_URL) {
    return process.env.NEXT_PUBLIC_API_URL.replace(/\/api$/, '')
  }

  // 自动识别模式：根据当前访问的域名自动构建API地址
  if (typeof window !== 'undefined') {
    const { protocol, hostname } = window.location
    const apiPort = process.env.NEXT_PUBLIC_API_PORT || '3001'
    return `${protocol}//${hostname}:${apiPort}`
  }

  // 服务端渲染时：使用环境变量配置构建完整URL
  const protocol = process.env.NEXT_PUBLIC_API_PROTOCOL || 'http'
  const host = process.env.NEXT_PUBLIC_API_HOST || 'localhost'
  const port = process.env.NEXT_PUBLIC_API_PORT || '3001'

  // 如果所有环境变量都为空，返回空字符串（使用相对路径）
  if (!protocol && !host && !port) {
    return ''
  }

  return `${protocol}://${host}:${port}`
}

/**
 * 获取文件 URL
 */
export function getFileUrl(path: string): string {
  if (!path) return ''

  // 确保 path 是字符串类型
  const pathStr = typeof path === 'string' ? path : String(path)

  if (pathStr.startsWith('http://') || pathStr.startsWith('https://')) return pathStr
  const baseUrl = getApiBaseUrl()
  // 确保路径以 / 开头
  const normalizedPath = pathStr.startsWith('/') ? pathStr : `/${pathStr}`
  return `${baseUrl}${normalizedPath}`
}

/**
 * 安全的 fetch 包装器，处理服务器端相对路径问题，支持客户端缓存和请求去重
 * @param url - 请求地址
 * @param options - fetch 选项
 * @param useCache - 是否使用缓存（默认 true）
 * @param cacheTTL - 自定义缓存时间（毫秒），不传则使用系统配置
 */
export async function safeFetch(
  url: string,
  options?: RequestInit,
  useCache: boolean = true,
  cacheTTL?: number
): Promise<Response> {
  const method = options?.method?.toUpperCase() || 'GET'
  const isGetRequest = method === 'GET'

  // 只有 GET 请求才使用缓存和去重
  if (isGetRequest && useCache && typeof window !== 'undefined') {
    // 尝试从缓存获取
    const cached = clientCache.get(url)
    if (cached) {
      // console.log(`✓ 缓存命中: ${url}`)
      // 返回模拟的 Response 对象
      return new Response(JSON.stringify(cached), {
        status: 200,
        headers: { 'Content-Type': 'application/json' }
      })
    }

    // 使用请求去重
    return clientCache.dedupeRequest(url, async () => {
      return performFetch(url, options, isGetRequest, useCache, cacheTTL)
    })
  }

  // 非GET请求或不使用缓存，直接执行
  return performFetch(url, options, isGetRequest, useCache, cacheTTL)
}

/**
 * 执行实际的fetch请求
 */
async function performFetch(
  url: string,
  options?: RequestInit,
  isGetRequest: boolean = true,
  useCache: boolean = true,
  cacheTTL?: number
): Promise<Response> {
  // 如果已经是完整URL，直接使用
  if (url.startsWith('http://') || url.startsWith('https://')) {
    const response = await fetch(url, options)

    // 缓存成功的 GET 响应
    if (isGetRequest && useCache && response.ok && typeof window !== 'undefined') {
      try {
        const clone = response.clone()
        const data = await clone.json()
        await clientCache.set(url, data, cacheTTL)
        // console.log(`✓ 已缓存: ${url}`)
      } catch (error) {
        // 如果响应不是 JSON，忽略缓存
      }
    }

    return response
  }

  // 服务器端：构造完整URL
  if (typeof window === 'undefined') {
    const apiUrl = process.env.NEXT_PUBLIC_API_URL

    // 如果配置的是相对路径（如 /api），需要构建完整URL指向后端端口
    if (apiUrl && apiUrl.startsWith('/')) {
      const protocol = process.env.NEXT_PUBLIC_API_PROTOCOL || 'http'
      const host = process.env.NEXT_PUBLIC_API_HOST || 'localhost'
      const port = process.env.NEXT_PUBLIC_API_PORT || '3001'
      return fetch(`${protocol}://${host}:${port}${url}`, options)
    }

    // 如果配置的是完整URL，直接拼接
    const baseUrl = getApiBaseUrl()
    const fullUrl = url.startsWith('/') ? `${baseUrl}${url}` : `${baseUrl}/${url}`
    return fetch(fullUrl, options)
  }

  // 客户端：直接使用相对路径
  const response = await fetch(url, options)

  // 缓存成功的 GET 响应
  if (isGetRequest && useCache && response.ok) {
    try {
      const clone = response.clone()
      const data = await clone.json()
      await clientCache.set(url, data, cacheTTL)
      // console.log(`✓ 已缓存: ${url}`)
    } catch (error) {
      // 如果响应不是 JSON，忽略缓存
    }
  }

  return response
}

/**
 * 验证邮箱
 */
export function isValidEmail(email: string): boolean {
  const regex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
  return regex.test(email)
}

/**
 * 验证 URL
 */
export function isValidUrl(url: string): boolean {
  try {
    new URL(url)
    return true
  } catch {
    return false
  }
}

/**
 * 生成随机 ID
 */
export function generateId(): string {
  return Math.random().toString(36).substring(2, 9)
}

/**
 * 防抖函数
 */
export function debounce<T extends (...args: any[]) => any>(
  func: T,
  wait: number
): (...args: Parameters<T>) => void {
  let timeout: NodeJS.Timeout | null = null

  return function executedFunction(...args: Parameters<T>) {
    const later = () => {
      timeout = null
      func(...args)
    }

    if (timeout) clearTimeout(timeout)
    timeout = setTimeout(later, wait)
  }
}

/**
 * 节流函数
 */
export function throttle<T extends (...args: any[]) => any>(
  func: T,
  limit: number
): (...args: Parameters<T>) => void {
  let inThrottle: boolean = false

  return function executedFunction(...args: Parameters<T>) {
    if (!inThrottle) {
      func(...args)
      inThrottle = true
      setTimeout(() => (inThrottle = false), limit)
    }
  }
}

/**
 * 深拷贝
 */
export function deepClone<T>(obj: T): T {
  return JSON.parse(JSON.stringify(obj))
}

/**
 * 获取图标类名（用于社交媒体图标）
 */
export function getSocialIcon(platform: string): string {
  const icons: Record<string, string> = {
    github: 'github',
    twitter: 'twitter',
    linkedin: 'linkedin',
    facebook: 'facebook',
    instagram: 'instagram',
    youtube: 'youtube',
    weibo: 'weibo',
    wechat: 'message-circle',
    email: 'mail',
    website: 'globe',
  }

  return icons[platform.toLowerCase()] || 'link'
}
